}

.nav-links a:hover,
.nav-links a:active,
.nav-links a:active{
    border-bottom: 4px solid black;
    font-weight: 600;
}

.nav-links button {
    cursor: pointer;
    border: 1px solid #292929;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
