.container{
  width:1200px;
  display:grid;
  grid-template-columns:repeat(auto-fit,1fr);
  grid-gap:15px;
  margin:0 auto;
}

.container .card{
  position:relative;
  width:300px;
  height:400px;
  margin:0 auto;
  background:#fff;
  box-shadow: 0 15px 50px rgba(0,0,0,0.8);
}

.container .card .face{
  position:absolute;
  width:100%;
  height:400px;
  bottom:0;
  left:0;
  display:flex;
  justify-content:center;
  align-items:center;
}

.container .card .face.face1{
  box-sizing:border-box;
  padding:20px;
}

.container .card .face.face1 h2{
  margin:0;
  padding:0;
}

.container .card .face.face2{
  background-image: url(/static/media/home_page2.b4106ede.jpg);
  background-position: center;
background-repeat: no-repeat;
background-size: cover;
 transition: 1s;
}

.container .card:hover .face.face2{
  height:60px;
}

.container .card .face.face2::before{
  content:'';
  position:absolute;
  left:0;
  top:0;
  width:50%;
  height:100%;
}

.container .card .face.face2 h2{
  margin:0;
  padding:0;
  font-size: 1rem;
  color:#fff;
  transition:0.5s;
}

